import picFull from '@/components/pic-full.vue'
export default {
	components: {
		picFull
	},
	data() {
		return {
			contacts: [],
			dictMap: {},
		}
	},
	onLoad(option = {}) {
		this.contacts = JSON.parse(option.contacts) || []
		this.dictMap = JSON.parse(option.dictMap)
	}
}
